import React, {
  useRef,
  useState,
  useEffect,
  Fragment,
  useCallback,
} from "react";
import Ttext from "../../../components/common/Ttext";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import * as actions from "../../../store/actions";
import {
  Box,
  Typography,
  Tooltip,
  IconButton,
  Paper,
  Chip,
} from "@material-ui/core";
import SquareButton from "../../../components/Buttons/SquareButton";
import { tableIcons } from "../../../util/tableIcons";
import MaterialTable from "material-table";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import DeleteRoundedIcon from "@material-ui/icons/DeleteRounded";
import i18n from "../../../i18n";
import IconTextNotice from "../../../components/common/IconTextNotice";
import IosSwitch from "../../../components/Inputs/IosSwitch";
import { arSA } from "date-fns/locale";
import TableSkeleton from "../../../components/common/TableSkeleton";
import GeneralDialog from "../../../components/common/GeneralDialog";
// import PageError from "../../../components/common/PageError";
import TranslateRoundedIcon from "@material-ui/icons/TranslateRounded";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="name" />,
    field: "language",
  },
  {
    title: <Ttext ns="settings" text="code" />,
    field: "code",
  },
  {
    title: "",
    field: "is_primary",
    render: (row) =>
      row.is_primary ? (
        <Chip
          size="small"
          label={<Ttext ns="settings" text="is_primary" />}
          color="primary"
          classes={{ label: "text-smaller" }}
        />
      ) : (
        ""
      ),
  },
];

let timeout;

const Languages = ({ location }) => {
  const [deleteDialog, setDeleteDialog] = useState({
    open: false,
    selected: null,
  });

  const languages = useSelector((state) => state.languages);
  const [mounted, setMounted] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();

  const fetchLanguages = useCallback(async () => {
    try {
      await dispatch(actions.getLanguages());
    } finally {
      setMounted(true);
    }
  }, [dispatch]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchLanguages();
    }, 0);
    return () => clearTimeout(timeout);
  }, [fetchLanguages]);

  const handleSwitchState = async (data) => {
    const id = data.language_id;
    const status = data.status > 0 ? 0 : 1;
    try {
      await dispatch(actions.switchLanguageStatus(id, status));
    } catch (err) {
      dispatch(actions.fireSnake("warning", err.message));
    }
  };

  const handleTriggerDeleteDialog = (selected) => {
    setDeleteDialog({
      open: !deleteDialog.open,
      selected: selected,
    });
  };

  const handleDeleteLanguage = async () => {
    const id = deleteDialog.selected.language_id;
    try {
      await dispatch(actions.deleteLanguage(id));
      handleTriggerDeleteDialog();
    } catch (err) {
      dispatch(actions.fireSnake("warning", err.message));
    }
  };

  return (
    <Fragment>
      <Typography variant="h4" className="py">
        {t("settings:languages_list")}
      </Typography>
      <Box className="flex flex-justify-end py">
        <Link to={`${location.pathname}/add`}>
          <SquareButton
            title={t("settings:new_language")}
            wIcon={<TranslateRoundedIcon className="text-light" />}
            py={0.6}
            px={0.8}
            type="primary"
          />
        </Link>
      </Box>
      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} />
        </Paper>
      ) : (
        // : !languages.loading && languages.errors ? (
        //   <PageError
        //     text={languages.errors.message || t("common:uncommon_err")}
        //     onTryAgain={fetchLanguages}
        //   />
        // )
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={languages.languages}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const language = props.data;
              return (
                <>
                  <Tooltip title={t("common:edit")}>
                    <Link
                      to={`${location.pathname}/edit/${language.language_id}`}
                    >
                      <IconButton>
                        <EditRoundedIcon color="secondary" />
                      </IconButton>
                    </Link>
                  </Tooltip>
                  <Tooltip title={t("common:delete")}>
                    <IconButton
                      onClick={() => handleTriggerDeleteDialog(language)}
                    >
                      <DeleteRoundedIcon color="error" />
                    </IconButton>
                  </Tooltip>
                  <IosSwitch
                    state={language.status}
                    onChange={() => handleSwitchState(language)}
                    name="status"
                    loading={languages.itemLoading}
                  />
                </>
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: languages.languages.length,
            pageSizeOptions: [languages.languages.length],
            thirdSortClick: false,
            showTitle: false,
            sorting: false,
          }}
          totalCount={languages.languages.length}
          page={0}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: languages.loading
                ? t("common:loadingggg")
                : !languages.loading &&
                  !languages.errors && (
                    <IconTextNotice
                      Icon={TranslateRoundedIcon}
                      text={t("settings:no_languages")}
                    />
                  ),
              dateTimePickerLocalization: i18n.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={languages.loading}
        />
      )}
      <GeneralDialog
        dialogOpen={deleteDialog.open}
        confirmText={t("common:confirm")}
        title={t("common:confirm_delete")}
        onCancel={() => handleTriggerDeleteDialog()}
        onConfirm={handleDeleteLanguage}
        loading={languages.itemLoading}
        disableBackdropClick
      />
    </Fragment>
  );
};

export default Languages;
